import React from 'react'
import { Tab } from 'react-mdl'
import {Tabs, Grid, Cell} from 'react-mdl'
import CardContent from '../component/CardContent.js'
class About extends React.Component{
    constructor(props){
        super(props)
        this.state = {activeTab:0}
    }
        toggleCategories(){
            if(this.state.activeTab === 0){
                return (
                    <div className='card-align'>
                    <CardContent projectTitle='Sathi' projectText="Dating site using firebase where admin can CRUD users." projectImg='https://image.shutterstock.com/image-photo/image-260nw-1397241389.jpg' projectLink='https://github.com/ishambhandari/sathi-public'/>
                    <CardContent projectTitle='Ecommerce' projectText="Personal Ecommerce test website implementing cart and payment" projectImg='https://static.semrush.com/blog/uploads/media/da/91/da9196e876bdcd82959c68c941ae3a26/ecommerce-blog-042017.jpg'projectLink='https://github.com/ishambhandari/ecommerce'/>
                    <CardContent projectTitle='Real State Lisiting' projectText="On going real state using Django as backend and React as frontend." projectImg='data:image/png;base64,'projectLink='https://github.com/ishambhandari/realstatelist'/>

                    </div>
                )}
            else if(this.state.activeTab === 1){
                return (
                    <div className='card-align'>
                    <CardContent projectTitle='Personal Portfolio' projectText="This is a personal portfolio site." projectImg='https://www.themarketingsage.com/wp-content/uploads/2015/08/about-me-leon-severan-we-buy-houses.jpg' projectLink='https://github.com/ishambhandari/perosal-portfolio'/>
                    <CardContent projectTitle='Javascript' projectText="Java Script project" projectImg='https://upload.wikimedia.org/wikipedia/commons/thumb/9/99/Unofficial_JavaScript_logo_2.svg/1024px-Unofficial_JavaScript_logo_2.svg.png'projectLink='https://github.com/'/>



                    </div>
                )}
            else{
                return (
                    <div className='card-align'>
                    <CardContent projectTitle='Bash Scripting' projectText="Bash Scripting project" projectImg='data:image/jpeg;base64,' projectLink='https://github.com/'/>
    
                    </div>
                )
            
        
        }
        
}
    render(){
        return(
            <div className='categories'>
                <Tabs activeTab={this.state.activeTab} onChange = {(tabId => this.setState({activeTab:tabId}))} ripple>
                <Tab>Python</Tab>
                <Tab>Javascript</Tab>
                <Tab>Bash Scripting</Tab>
                
                </Tabs>
                <section className='projects'>
                    <Grid className='projects'>
                        <Cell col={12}>
                            <div className='content'>
                            {this.toggleCategories()}

                            </div>
                        </Cell>
                    </Grid>
                </section>
            </div>
        )
    }
}
export default About